.phonedirectory {
	background-color: black;
	height: 50px;
	text-align: center;
	color: white;
	font-size: 1.8rem;
}

.button {
	border: none;
	background-color: #00FF00;
	color: white;
	width: 60px;
	margin: auto;
	display: inline-block;
	cursor: pointer;
}
.button:hover {
	background-color: #7CFC00;
} 

.goback {
	border: none;
	background-color: #FOF8FF;
	color: white;
	margin: auto;
	display: inline-block;
	cursor: pointer;
}

.goback:hover {
	background-color: #CD5C5C;
} 
.delete {
    border: none;
	background-color: #B22222;
	color: white;
	width: 60px;
	margin: auto;
	display: inline-block;
	cursor: pointer;
}
.delete:hover {
	background-color: #8B0000;
}

 h1 {
	font-size: 1.2rem;
